body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 2px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #050505; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #555;
  border-radius: 1px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.App {
  padding: 0;
  margin: 0;
  height: 100%;
  background-color: #080808;
}
.groundControl {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
}

h1, h2, h3, p {
  color: #e5e5e5;
}

.home {
  position: fixed;
  top: 21px;
  left: 21px;
  padding: 13px 21px;
  background: #e5e5e5;
  color: #080808;
  border-radius: 8px;
  font-weight: 800;
  transition: .2s ease-in-out;
  text-decoration: none;
}
.home:hover {
  background: #000;
  color: #e5e5e5;
}
